.nav{
    position: fixed;
    top: 0;
    width: 100%;
    padding:20px;
    height: 30px;
    z-index:1;

    display: flex;
    justify-content: space-between;

    /* Animations */
    transition-timing-function:ease-in;
    transition:all 0.5s;

}

.nav_logo{
    position: fixed;
    left: 20px;
    width: 80px;
    object-fit: contain;
}

.nav_avatar{
    position: fixed;
    width: 30px;
    right: 20px;
    object-fit: contain;
}

.nav_black{
    background-color: #111;
}